















































var _self;
import {
	mapMutations
} from 'vuex'
var graceChecker = require("../../graceUI/graceChecker.js");
export default {
	data() {
		return {
			positionTop: 500, //默认屏幕高度500
			countNum: 120,
			countDownTimer: null,
			phoneno: '',
			password: '',
			btnLoading: false
		}
	},
	computed: {
		margin() {
			return this.positionTop * 0.08;
		}
	},
	onReady() {
		_self = this;
		this.positionTop = uni.getSystemInfoSync().windowHeight - 105;
	},
	methods: {
		...mapMutations(['login', 'setOpenId']),
		loginWithWx: function() {
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					_self.thirdLogin(loginRes.authResult.openid, 0);
				}
			});
		},
		loginWithQQ: function() {
			uni.login({
				provider: 'qq',
				success: function(loginRes) {
					console.log(JSON.stringify(loginRes));
					_self.thirdLogin(loginRes.authResult.openid, 1);
				}
			});
		},
		loginWithWb: function() {
			uni.showToast({
				title: '微博登陆审核中...',
				icon: 'none'
			});
		},
		thirdLogin: function(openId, type) {
			uni.showLoading({
				title: '登录中...'
			});
			uni.request({
				url: this.GLOBAL.serverSrc + 'common/login/verifyopenid',
				method: 'GET',
				data: {
					openId: openId,
					type: type
				},
				success: res => {
					if (res.data.status === 200) {
						this.login(res.data);
						uni.reLaunch({
							url: '../index/index'
						});
					} else if (res.data.status === 201) { //若openid未绑定
						var playload = new Object();
						playload.openId = openId;
						playload.type = type;
						this.setOpenId(playload);
						uni.navigateTo({
							url: './logintemp?type=' + type
						});
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						});
					}
				},
				fail: (e) => {
					this.GLOBAL.requestFail(e);
				},
				complete: () => {
					uni.hideLoading();
				}
			});
		},
		loginNow: function(e) {
			// 表单验证
			var rule = [{
				name: "phone",
				checkType: "phoneno",
				errorMsg: "请输入正确的手机号"
			}, {
				name: "password",
				checkType: "string",
				checkRule: "8,20",
				errorMsg: "密码长度应为8-20个字符"
			}];
			var formData = e.detail.value;
			var checkRes = graceChecker.check(formData, rule);
			// 验证通过
			if (checkRes) {
				this.btnLoading = true;
				uni.request({
					url: this.GLOBAL.serverSrc + 'common/login/login',
					method: 'POST',
					data: e.detail.value,
					success: res => {
						if (res.data.status === 200) {
							this.login(res.data);
							uni.reLaunch({
								url: '../index/index'
							});
						} else {
							uni.showToast({
								title: res.data.msg,
								icon: "none"
							});
						}
					},
					fail: (e) => {
						this.GLOBAL.requestFail(e);
					},
					complete: () => {
						this.btnLoading = false;
					}
				});
			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
			}
		},
		reg: function() {
			uni.navigateTo({
				url: '../register/reg',
			});
		}
	}
}
